<template>
  <div id="fabric-create-project-dialog-component">
    <el-dialog
      title="Initiate Project"
      v-model="visible"
      width="40%"
      top="45px"
    >
      <div class="inputs-container">
        <el-row :gutter="30">
          <el-col :span="24">
            <p class="label">Name</p>
            <el-input v-model="form.name" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Expected Finish Time</p>
            <el-date-picker
              v-model="form.expected_end_at"
              value-format="YYYY-MM-DD"
              placeholder="選擇日期"
            >
            </el-date-picker>
          </el-col>
          <el-col :span="24">
            <p class="label">Price</p>
            <el-input v-model="form.price" type="number" clearable></el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Brand</p>
            <el-select v-model="form.brand">
              <el-option
                v-for="item in brandOptions"
                :key="item.id"
                :value="item.value"
                :label="item.label"
              ></el-option>
            </el-select>
          </el-col>
          <el-col :span="24">
            <p class="label">Garment Factory</p>
            <el-select v-model="form.garment_factory">
              <el-option
                v-for="item in garmentFactoryOptions"
                :key="item.id"
                :value="item.value"
                :label="item.label"
              ></el-option>
            </el-select>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="visible = false">Cancel</el-button>
          <el-button type="success" @click="onConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useRouter } from "vue-router";

import { getAvaliableOptions, postCreateProject } from "@/apis/project";
import { ProjectCreateInputs } from "@/typings/project";
import { FabricDetailResponse } from "@/typings/fabric";
import { OptionItem } from "@/typings/asideFilter";

const Component = defineComponent({
  name: "FabricCreateProjectDialog",
  setup() {
    const router = useRouter();
    const visible = ref(false);

    const form = reactive<ProjectCreateInputs>({
      name: "",
      expected_end_at: null,
      price: 0,
      start_fabric: 0,
      brand: null,
      garment_factory: null
    });

    const brandOptions = ref<OptionItem[]>([]);
    const garmentFactoryOptions = ref<OptionItem[]>([]);

    const init = async () => {
      const brandResponse = await getAvaliableOptions("brand");
      const garmentFactoryResponse = await getAvaliableOptions(
        "garment_factory"
      );
      brandOptions.value = brandResponse.data.brands.map(
        (it: { name: string; id: number }): OptionItem => ({
          label: it.name,
          value: it.id,
          checked: false
        })
      );
      garmentFactoryOptions.value = garmentFactoryResponse.data.brands.map(
        (it: { name: string; id: number }): OptionItem => ({
          label: it.name,
          value: it.id,
          checked: false
        })
      );
    };

    const open = async (data: FabricDetailResponse) => {
      visible.value = true;
      form.start_fabric = data.id;
      await init();
    };

    const onConfirm = async () => {
      const { data } = await postCreateProject(form);
      visible.value = false;
      await router.push({ name: "ProjectDetail", params: { id: data.id } });
    };

    return {
      form,
      visible,
      brandOptions,
      garmentFactoryOptions,

      open,
      onConfirm
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#fabric-create-project-dialog-component
  .inputs-container
    padding 0 10%
    .label
      text-align start
      font-size $font-size-large
      font-weight 500
      padding 0 8px
      margin-bottom 15px

    :deep() .el-col
      margin-bottom 30px

    :deep() .el-input
      width 100%

    :deep() .el-select
      width 100%
</style>
