<template>
  <div v-if="image" class="fabric-image-with-3d-mode-component">
    <Fabric3DMode v-if="is3DMode" :image="image"></Fabric3DMode>
    <img v-else :src="image + '@500-500'" alt="" />
    <h3 class="toggle" @click="onClick">{{ is3DMode ? "Picture" : "3D" }}</h3>
  </div>
</template>

<script>
import { ref } from "vue";
import Fabric3DMode from "@/components/Fabric3DMode.vue";

export default {
  name: "Avatar",
  components: {
    Fabric3DMode
  },
  props: {
    image: {
      type: String,
      default: ""
    }
  },
  setup() {
    const is3DMode = ref(false);

    const onClick = () => {
      is3DMode.value = !is3DMode.value;
    };
    return {
      is3DMode,

      onClick
    };
  }
};
</script>

<style lang="stylus" scoped>
.fabric-image-with-3d-mode-component
  position relative
  width 100%
  height 100%

  img
    width 100%
    height 100%

  .toggle
    position absolute
    flex()
    right 15px
    top 15px
    cursor pointer
    background white
    width 80px
    height 30px
    border-radius 15px
</style>
