<template>
  <div id="fabric-detail-component" v-if="detail">
    <el-row :gutter="60">
      <el-col :span="12">
        <div class="fabric-images-container">
          <div class="main-image-container">
            <div class="main-image">
              <FabricImageWith3DMode :image="mainFile"></FabricImageWith3DMode>
            </div>
          </div>
          <el-carousel
            height="150px"
            :autoplay="false"
            arrow="always"
            indicator-position="none"
          >
            <el-carousel-item
              v-for="(item, index) in getSecondaryMedia(detail)"
              :key="index"
            >
              <div class="carouse-item">
                <img
                  v-for="(_item, _index) in item"
                  :key="_index"
                  class="secondary-image"
                  :class="{ active: _item.id === selectedId }"
                  :src="_item.file + '@250-250'"
                  :alt="_item.name"
                  @click="onCarouseItemClick(_item)"
                />
              </div>
            </el-carousel-item>
          </el-carousel>
        </div>
      </el-col>

      <el-col class="content-col" :span="12">
        <div class="content-container">
          <div class="title-container">
            <h2 class="title">{{ detail.name }} - {{ detail.art_no }}</h2>
            <el-button
              v-permission="'project_add'"
              type="success"
              @click="onCreateProjectClick"
              >Initiate Project</el-button
            >
          </div>
          <div class="info-container">
            <div class="item">
              <font-awesome-icon
                class="icon"
                :icon="['fas', 'eye']"
              ></font-awesome-icon>
              <p>1258</p>
            </div>
            <div class="item item-cursor" @click="onAddToFavoriteClick">
              <font-awesome-icon
                v-if="detail.is_favorite"
                class="icon"
                :style="{ color: '#ef796c' }"
                :icon="['fas', 'heart']"
              ></font-awesome-icon>
              <font-awesome-icon
                v-else
                class="icon"
                :style="{ color: '#ef796c' }"
                :icon="['far', 'heart']"
              ></font-awesome-icon>
              <p>Favorite</p>
            </div>
            <div class="item item-cursor" @click="onCopyLinkClick">
              <img
                class="icon"
                src="@/assets/images/icons/png/link.png"
                alt=""
              />
              Copy
            </div>
            <el-select
              v-permission="'fabric_status_update'"
              class="status-select"
              v-model="statusValue"
              @change="onStatusChange"
            >
              <el-option
                v-for="item in statusOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>

          <!--Detail info-->
          <p class="description">
            <span>Content:</span>
            {{ detail.real_composition }}
          </p>
          <div class="details-container">
            <p class="detail">
              <span>Width 1:</span>
              {{ detail.width_1 }}
            </p>
            <p class="detail">
              <span>Weight y:</span>
              {{ detail.weight_y }}
            </p>
            <p class="detail">
              <span>Finished Specification:</span>
              {{ detail.finished_specification }}"
            </p>
            <p class="detail">
              <span>Grey Specification:</span>
              {{ detail.grey_specification }}
            </p>
          </div>
        </div>

        <div class="colors-container">
          <div v-for="(color, index) in colors" :key="index" class="color-item">
            <div
              class="color-circle"
              :style="{ background: color.color }"
            ></div>
            <p class="color-text">{{ color.text }}</p>
          </div>
        </div>

        <div class="hashtags-container">
          <div
            v-for="(hashtag, index) in hashtags"
            :key="index"
            class="hashtag-item"
          >
            <span>#</span>
            {{ hashtag }}
          </div>
        </div>

        <div class="reports-container" v-permission="'fabric_file_download'">
          <div
            v-for="(item, index) in detail.reports"
            :key="index"
            class="report"
            @click="onDownloadClick(item.file)"
          >
            <font-awesome-icon
              class="icon"
              :icon="['far', 'file']"
            ></font-awesome-icon>
            <p>{{ item.name }}</p>
          </div>
        </div>
      </el-col>
    </el-row>

    <div class="projects-container">
      <el-row :gutter="40">
        <el-col :span="12" v-permission="'fabric_related_collection_view'">
          <h3 class="zoom-title">Related Collection</h3>
          <div
            v-for="item in relatedCollections"
            :key="item.id"
            class="project"
            @click="onCollectionClick(item.id)"
          >
            <font-awesome-icon
              class="icon"
              :icon="['fas', 'border-all']"
            ></font-awesome-icon>
            <p>{{ item.name }}</p>
          </div>
        </el-col>
        <el-col :span="12" v-permission="'fabric_related_project_view'">
          <h3 class="zoom-title">Related project</h3>
          <div
            v-for="index in 3"
            :key="index"
            class="project"
            @click="onProjectClick"
          >
            <font-awesome-icon
              class="icon"
              :icon="['far', 'file']"
            ></font-awesome-icon>
            <p>Nike Q4</p>
          </div>
        </el-col>
      </el-row>
    </div>

    <div class="recommend-cards-container">
      <el-row :gutter="40">
        <el-col :span="24" v-permission="'fabric_similar_view'">
          <DoubleFabricCard
            title="Similar Fabrics"
            description="Description"
            :data="relates"
            :size="4"
            @expand="onExpandClick"
          ></DoubleFabricCard>
        </el-col>
        <el-col :span="12" v-permission="'fabric_family_view'">
          <DoubleFabricCard
            title="Family Fabrics"
            description="Description"
            :data="relates"
            @expand="onExpandClick"
          ></DoubleFabricCard>
        </el-col>
        <el-col :span="12" v-permission="'fabric_work_view'">
          <DoubleFabricCard
            title="Fabric Works"
            description="Description"
            :data="relates"
            @expand="onExpandClick"
          ></DoubleFabricCard>
        </el-col>
      </el-row>
    </div>

    <el-dialog v-model="dialog.visible" width="80%">
      <template #title>
        <h2 class="title">{{ dialog.title }}</h2>
        <h4 class="description">{{ dialog.description }}</h4>
      </template>
      <el-row :gutter="60">
        <el-col
          class="col"
          v-for="item in dialog.list"
          :key="item.id"
          :span="6"
        >
          <FabricCard :data="item"></FabricCard>
        </el-col>
      </el-row>
    </el-dialog>

    <CreateProjectDialog ref="createProjectDialog"></CreateProjectDialog>
  </div>
</template>

<script lang="ts">
import {
  defineComponent,
  toRefs,
  ref,
  reactive,
  computed,
  watch,
  Ref,
  onMounted
} from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import * as _ from "ramda";

import { loading } from "@/utils";
import { ElNotification } from "element-plus";
import CreateProjectDialog from "./CreateProjectDialog.vue";
import DoubleFabricCard from "@/components/DoubleFabricCard.vue";
import FabricCard from "@/components/FabricCard.vue";
import FabricImageWith3DMode from "@/components/FabricImageWith3DMode.vue";

import { OptionItem } from "@/typings/asideFilter";
import {
  GetFabric,
  GetFabricRelates,
  PostFabricStatus,
  PostAddFabricToFavorite,
  SetFabricDetail,
  GetFabricRelatedCollections
} from "@/typings/store";
import {
  FabricDetailResponse,
  FabricFileResponse,
  FabricRelatedCollectionResponse,
  FabricRelatesResponse,
  StatusChangeParams
} from "@/typings/fabric";

import { copyToClipboard } from "@/utils/clipboard";
import { iCreateDialog } from "@/typings/component";

const statusOptions: OptionItem[] = [
  { label: "Public", value: "public", checked: false },
  { label: "Private", value: "private", checked: false }
];

const colors = [
  { color: "#85776e", text: "PANTONE 14-4112 Skyway" },
  { color: "#85776e", text: "PANTONE 14-4112 Skyway" },
  { color: "#85776e", text: "PANTONE 14-4112 Skyway" }
];

const hashtags = [
  "regularfinish",
  "regularfinish",
  "regularfinish",
  "regularfinish",
  "regularfinish",
  "regularfinish"
];

type ExpandData = {
  visible: boolean;
  title: string;
  description: string;
  list: FabricRelatesResponse[];
};

const Component = defineComponent({
  name: "FabricDetail",
  components: {
    CreateProjectDialog,
    DoubleFabricCard,
    FabricCard,
    FabricImageWith3DMode
  },
  props: {
    id: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const router = useRouter();
    const store = useStore();
    const createProjectDialog: Ref<iCreateDialog | null> = ref(null);

    const statusValue = ref("");
    const selectedId = ref(-1);

    const { id } = toRefs(props);

    const dialog = reactive<ExpandData>({
      visible: false,
      title: "",
      description: "",
      list: []
    });
    const mainFile = ref("");

    const detail = computed<FabricDetailResponse>(
      () => store.state.fabric.detail
    );
    const relates = computed<FabricRelatesResponse[]>(
      () => store.state.fabric.relates
    );
    const relatedCollections = computed<FabricRelatedCollectionResponse[]>(
      () => store.state.fabric.relatedCollections
    );

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetFabric, id.value);
        await store.dispatch(GetFabricRelates, id.value);
        await store.dispatch(GetFabricRelatedCollections, id.value);
        statusValue.value = detail.value.status;
        mainFile.value = detail.value.media.find(it => it.main)!.file;
        selectedId.value = detail.value.media.find(it => it.main)!.id;
      } catch (_) {
        await router.replace({ name: "Error" });
      } finally {
        loadingInstance.close();
      }
    };

    watch(id, init);

    onMounted(init);

    const getSecondaryMedia = (detail: FabricDetailResponse) => {
      return detail.media.reduce(
        (result, item: FabricFileResponse, index: number) => {
          if (index % 3) {
            (result[result.length - 1] as FabricFileResponse[]).push(item);
          } else {
            (result as FabricFileResponse[][]).push([item]);
          }
          return result;
        },
        []
      );
    };

    const onStatusChange = async (value: string) => {
      const params: StatusChangeParams = { id: id.value, status: value };
      const { status } = await store.dispatch(PostFabricStatus, params);
      if (status) {
        ElNotification({
          title: "Success",
          type: "success",
          message: `Change status to ${status} successful.`
        });
      } else {
        statusValue.value = value === "public" ? "private" : "public";
        ElNotification({
          title: "Error",
          type: "error",
          message: `Change status Failed.`
        });
      }
    };

    const onCopyLinkClick = () => {
      const href = window.location.href;
      copyToClipboard(href);
      ElNotification({
        title: "Success",
        type: "success",
        message: `Your Copied Link ${href}`
      });
    };

    const onAddToFavoriteClick = async () => {
      const { status, message } = await store.dispatch(
        PostAddFabricToFavorite,
        id.value
      );
      if (status === "success") {
        store.commit(SetFabricDetail, {
          ...detail.value,
          is_favorite: message.includes("add")
        });
      } else {
        ElNotification({
          title: "Error",
          type: "error",
          message
        });
      }
    };

    const onCreateProjectClick = async () => {
      if (!createProjectDialog.value) return;
      createProjectDialog.value.open(detail.value);
    };

    const onCarouseItemClick = (item: FabricFileResponse) => {
      mainFile.value = item.file;
      selectedId.value = item.id;
    };

    const onDownloadClick = (url: string) => {
      window.open(url, "blank");
    };

    const onCollectionClick = async (id: number) => {
      await router.push({ name: "CollectionDetail", params: { id } });
    };

    const onProjectClick = async (id: number) => {
      await router.push({ name: "ProjectDetail", params: { id } });
    };

    const onExpandClick = (data: ExpandData) => {
      dialog.visible = data.visible;
      dialog.title = data.title;
      dialog.description = data.description;
      dialog.list = data.list;
    };

    return {
      createProjectDialog,

      dialog,
      detail,
      relates,
      relatedCollections,
      mainFile,
      statusValue,
      statusOptions,
      selectedId,

      colors,
      hashtags,

      onStatusChange,
      onCopyLinkClick,
      onAddToFavoriteClick,
      onCreateProjectClick,
      onCarouseItemClick,
      onDownloadClick,
      onCollectionClick,
      onProjectClick,
      onExpandClick,
      getSecondaryMedia,
      _
    };
  }
});

export default Component;
</script>

