
import { ref, toRefs, onMounted, onBeforeUnmount, watchEffect } from "vue";
import * as THREE from "three";
import { Group, Material, Mesh, Object3D, Texture } from "three";
import { GLTF, GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import ShirtGltf from "@/assets/three/shirt/scene.gltf";
import { formatImageUrl } from "@/utils";

export default {
  name: "Fabric3DMode",
  props: {
    image: {
      type: String,
      default: ""
    }
  },
  setup(props: any) {
    const { image } = toRefs(props);
    const isAlive = ref(true);
    const listener = ref();
    const texture = ref(new Texture());
    const objectName = ref("");

    // *** Init
    // Renderer
    const renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.setClearColor(0xeeeeee, 1.0);
    renderer.outputEncoding = THREE.sRGBEncoding;
    renderer.toneMapping = THREE.ACESFilmicToneMapping;

    // 場地
    const scene = new THREE.Scene();

    // 鏡頭
    const camera = new THREE.PerspectiveCamera(
      45,
      window.innerWidth / window.innerHeight,
      0.1,
      100
    );
    camera.position.set(0.6, 0.6, 0.6);
    camera.lookAt(scene.position);

    // 光源
    const pointLight = new THREE.PointLight(0xffffff);
    pointLight.position.set(1, 1, 1);
    pointLight.power = 20;
    scene.add(pointLight);

    // 隔線
    const grid = new THREE.GridHelper(5, 10, 0x000000, 0x000000);
    const gridMaterial = grid.material as Material;
    gridMaterial.opacity = 0.1;
    gridMaterial.depthWrite = false;
    gridMaterial.transparent = true;
    scene.add(grid);

    // 模型
    const gltfLoader = new GLTFLoader();

    // 布料貼圖
    const textureLoader = new THREE.TextureLoader();
    textureLoader.setCrossOrigin("");

    // ***

    const createAnimate = (model: Object3D) => {
      const animate = () => {
        if (isAlive.value) {
          requestAnimationFrame(animate);
          model.rotation.y += 0.01;
          renderer.render(scene, camera);
        }
      };
      animate();
    };

    const loadModel = (model: string) => {
      gltfLoader.load(model, (gltf: GLTF) => {
        const object = gltf.scene;
        objectName.value = object.name;
        const box = new THREE.Box3().setFromObject(object);
        const center = box.getCenter(new THREE.Vector3());
        object.position.x += object.position.x - center.x;
        object.position.y += object.position.y - center.y;
        object.position.z += object.position.z - center.z;
        object.traverse((o: Object3D | Mesh) => {
          if (o.type === "Mesh") {
            // @ts-ignore
            (o as Mesh).material.map = texture.value;
          }
        });
        scene.add(object);
        createAnimate(object);
      });
    };

    const changeTexture = (image: string) => {
      const _texture = textureLoader.load(formatImageUrl(image) + "@250-250");
      _texture.needsUpdate = true;
      _texture.flipY = false;
      _texture.wrapS = THREE.RepeatWrapping;
      _texture.wrapT = THREE.RepeatWrapping;
      _texture.repeat.set(5, 5); // x, y 軸分別要貼幾次

      texture.value = _texture;
    };

    const init = (image: string, model: string) => {
      scene.remove(scene.getObjectByName(objectName.value) as Object3D);
      changeTexture(image);
      loadModel(model);
    };

    watchEffect(() => {
      init(image.value, ShirtGltf);
    });

    onMounted(() => {
      const containerElement = document.getElementById("fabric-3d-mode");
      if (!containerElement) return;
      renderer.setSize(
        containerElement.offsetWidth,
        containerElement.offsetHeight
      );
      containerElement.appendChild(renderer.domElement);

      // 監聽螢幕寬高變化來做簡單 RWD 設定
      listener.value = window.addEventListener("resize", function() {
        camera.aspect = window.innerWidth / window.innerHeight;
        camera.updateProjectionMatrix();
        renderer.setSize(
          containerElement.offsetWidth,
          containerElement.offsetHeight
        );
      });
    });

    onBeforeUnmount(() => {
      isAlive.value = false;
      window.removeEventListener("resize", listener.value);
    });
  }
};
